<a-modal class="dm-wrap" :visible="visible" title="" width="680px" @cancel="handleCancel"><template #footer><a-button @click="handleCancel"></a-button><a-button type="primary" @click="handleConfirm" :loading="saving"></a-button></template><a-form :model="formState" :wraper-col="{span:21}" :label-col="{span:3}"><Row><Col span="12"><a-form-item label="" :label-col="{span:6}" :wraper-col="{span:18}" v-bind="formState.startTime"><a-date-picker v-model:value="formState.startTime" :disabledDate="startDisabled" :show-time="{format: 'HH:mm'}"></a-date-picker></a-form-item></Col><Col span="12"><a-form-item label="" :label-col="{span:6}" :wraper-col="{span:18}" v-bind="formState.endTime"><a-date-picker v-model:value="formState.endTime" :disabledDate="endDisabled" :show-time="{format: 'HH:mm'}"></a-date-picker></a-form-item></Col></Row><a-form-item label=""><a-input-number v-model:value="formState.runStageTotalTime"></a-input-number><ExclamationCircleOutlined style="margin:0 8px"></ExclamationCircleOutlined><span>2020</span></a-form-item><Row><Col span="6"><a-form-item label="" :label-col="{span:12}"  :wraper-col="{span:12}"><a-switch v-model:checked="formState.viewReport"></a-switch></a-form-item></Col><Col span="6"><a-form-item label="" :label-col="{span:12}"  :wraper-col="{span:12}"><a-switch v-model:checked="formState.viewAnswer"></a-switch></a-form-item></Col></Row><a-form-item label="" :labelCol="{ span: 3 }" :wrapperCol="{ span: 21 }"><div class="ant-input" style="margin:4px 0;display:block;height:auto;min-height:34px;"><a-tag v-for="cla in checkedClass" :key="cla.id" closable @close="handleTagClose(cla.id)">{{ cla.name }}</a-tag></div><div class="dic-main"><div class="dic-left"><template v-if="classList.length"><div class="dicl-item" v-for="(cla, claIdx) in classList" :key="cla.id" @click="handleClassClick(cla, claIdx)"><a-checkbox v-model:checked="cla.checked" @change="handleClassChange($event, claIdx)" :indeterminate="cla.indeterminate" :disabled="!cla.classMemberList.length || cla.disable" style="margin-right:4px"></a-checkbox><span>{{ cla.name }}</span></div></template></div><div class="dic-right"><template v-if="classList.length"><div class="dicr-item" v-for="stu in classList[activeClassIdx].classMemberList" :key="stu.userId"><a-checkbox v-model:checked="stu.checked" @change="handleStuChange"><span class="dicr-text">{{ stu.name }}</span></a-checkbox></div></template></div></div></a-form-item></a-form></a-modal>